import {SET_CURRENT_PAGE, SET_CURRENT_PAGE_TITLE, TOGGLE_MOBILE_NAV_VIEW} from './actionType'

export const setCurrentPageName = val => dispatch => {
    dispatch({
        type: SET_CURRENT_PAGE,
        payload: val,
    });
};
export const setCurrentPageTitle = val => dispatch => {
    dispatch({
        type: SET_CURRENT_PAGE_TITLE,
        payload: val,
    });
};
export const toggleMobileNavView = val => dispatch => {
    dispatch({
        type: TOGGLE_MOBILE_NAV_VIEW,
        payload: val,
    });
};